@import url("../../assets/styles/colors.css");

.wrapper {
  display: flex;
  width: 100%;
  height: 100%;
  flex-direction: column;
  align-items: center;
}

.items {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 15px 0;
  height: 100%;
  gap: 15px;
}

.empty {
  font-size: 46px;
  font-weight: 500;
}

.dark {
  color: var(--gray);
}

.light {
  color: var(--dark-back);
}
